

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #222831;
  font-family: sans-serif;
}
.calculator {
  width: 300px;
  padding-bottom: 15px;
  border-radius: 7px;
  background-color: #000;
  box-shadow: 5px 8px 8px -2px rgba(0, 0, 0, 0.61);
}
 
.row {
  display: flex;
  justify-content: space-between;
}


input[type="text"] {
  width: 100%;
  height: 80px;
  border: none;
  box-sizing: border-box;
  padding: 10px;
  font-size: 2rem;
  background-color:#99ff33;
  color: black;
  text-align: right;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}
 button {
  width: 50px;
  height: 50px;
  border-radius: 50%;  border: none;
  outline: none;
  font-size: 1.5rem;
  background-color: #222;
  color: #fff;
  margin: 10px;
}
button:hover {
  cursor: pointer;
}

.operator {
  background-color: #99ff33;
  color: #000f;
}
h1{
  font-size:30px;
  color:#99ff33;
  text-align:center;
}